.page {

  &__body {
    margin: 0;
  }

  &__content {
    height: 100vh;

    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__stopwatch {
    position: relative;
    width: $seconds-arrow-length;
    height: $seconds-arrow-length;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
